.btn {
  border-radius: 3px;
  border: none;
  font-weight: 500;
  outline: none;
  min-width: 148px;
  transition: all 0.3s;
  font-family: "Stolzl-Medium", sans-serif;
}

.btn:focus {
  box-shadow: none;
  outline: none;
}

.btn.btn-default {
  min-width: 220px;
  border: 1px solid #c3d0f4;
  font-size: 12px;
  line-height: 13px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  padding: 19px 0 16px;
  color: #8a9ab5;
  background: transparent;
}

.btn.btn-default:hover {
  background: #f4f9ff;
  border: 1px solid #d6e1ff;
}

.btn.btn-default:active {
  background: #e7f2ff;
  border: 1px solid #d6e1ff;
  box-shadow: none;
}

.btn.btn-default:focus {
  background: #e7f2ff;
  border: 1px solid #aabde7;
  box-shadow: 0 0 0 1px #aabde7;
}

.btn.btn-blue {
  min-width: 255px;
  background: #3c61e2;
  font-size: 15px;
  color: #ffffff;
  padding: 19px 0 17px;
  border: 1px solid #3c61e2;
}

.btn.btn-blue:hover {
  background: #3156d8;
}

.btn.btn-blue:active {
  background: #3156d8;
}

.btn.btn-blue:focus {
  background: #3c61e2;
  border: 1px solid #aabde7;
  box-shadow: 0 0 0 1px #aabde7;
}

.btn.btn-blue.ico-btn.ico-download {
  padding: 15px 0 17px;
}

.btn.btn-blue.ico-btn.ico-download span {
  display: inline-block;
  width: 15px;
  height: 20px;
  background: url("../images/ico-download.svg") 0 0 no-repeat;
  margin-right: 15px;
  transform: translate(0, 4px);
}

.btn.btn-blue.ico-btn.ico-mail span {
  display: inline-block;
  width: 16px;
  height: 12px;
  background: url("../images/ico-mail.svg") 0 0 no-repeat;
  margin-right: 10px;
  transform: translate(0, 2px);
}

.btn.btn-red {
  padding: 25px 40px;
  background: linear-gradient(180deg, #cb0000 19.61%, #7e0000 119.61%);
  border-radius: 8px;
  font-size: 1.25rem;
  color: #fff;
  position: relative;
}

.btn.btn-red:hover {
  color: #2d344d;
}

.btn.btn-red:before {
  content: "";
  position: absolute;
  top: -20px;
  left: -15px;
  width: 250px;
  height: 40px;
  background: rgba(255, 171, 171, 0.48);
  mix-blend-mode: screen;
  filter: blur(34.9235px);
  z-index: 1;
}

.btn.btn-red:after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 10px;
  width: 200px;
  height: 40px;
  background: rgba(122, 19, 19, 0.48);
  filter: blur(23.2823px);
  z-index: 1;
}

.has-highlight .btn {
  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
}

.has-highlight .btn:before {
  content: "";
  height: 200px;
  width: 250px;
  top: -20px;
  left: -140px;
  display: block;
  position: absolute;
  background: white;
  border-radius: 100%;
  transform: scale(0);
  z-index: 20;
  animation: ripple 4s linear infinite;
}

.btn-outline-blue {
  border: 1px solid #3c61e2;
  color: #3c61e2;
}

.btn-outline-blue:hover {
  background-color: #3c61e2;
  color: #fff;
}

.has-highlight {
  position: relative;
}

@media screen and (min-width: 374px) {
  .has-highlight::after {
    content: "";
    position: absolute;
    background-image: url("../images/arrow-icon-reverse.svg");
    background-repeat: no-repeat;
    background-size: cover;
    top: -30px;
    right: -50px;
    width: 40px;
    height: 68px;
  }
}

@keyframes ripple {
  20% {
    opacity: 0;
    transform: scale(2.5);
  }
  100% {
    opacity: 0;
    transform: scale(2.5);
  }
}

