.mega-menu {
  position: fixed;
  width: 100%;
  background: rgba(255, 255, 255, 0.98);
  backdrop-filter: blur(12px);
  transition: 0.5s;
  transform: translateY(70px);
  display: none;
}

.mega-menu.opened {
  display: block;
  left: 0;
  z-index: 9999;
  height: 100vh;
  transform: translateY(70px);
}

@media screen and (max-width: 991px) {
  .mega-menu.opened {
    transform: translateY(45px);
    overflow: scroll;
  }
}

.mega-menu a {
  color: #2d344d;
  display: inline-block;
}

.mega-menu-inner {
  padding-top: 55px;
  border-top: 1px solid #E7EDFF;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 991px) {
  .mega-menu-inner {
    padding-top: 25px;
    padding-bottom: 100px;
    flex-direction: column;
  }
}

.mega-menu .menu-wrap {
  padding-right: 10px;
  width: 235px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

@media screen and (max-width: 991px) {
  .mega-menu .menu-wrap {
    width: 100%;
    border-bottom: 1px solid #e7edff;
  }
  .mega-menu .menu-wrap:last-child {
    border: 0;
  }
}

