  .mega-menu .menu-wrap > ul > li > a {
    padding-right: 20px;
    color: #2D344D;
  }
}

@media screen and (max-width: 991px) {
  .mega-menu .menu-wrap > ul > li.menu-item-has-children .custom-arrow {
    display: block;
  }
}

.mega-menu .menu-wrap > ul > li.opened > a {
  color: #3c61e2;
}

.mega-menu .menu-wrap > ul > li.opened .custom-arrow:after {
  border-color: #3c61e2;
  transform: rotate(135deg);
}

