.tooltip {
  background-color: #263646;
  padding: 0 14px;
  line-height: 27px;
  position: absolute;
  border-radius: 4px;
  z-index: 100;
  color: #fff;
  font-size: 12px;
  transition: all 0.2s ease-in-out;
  @at-root .single-post__list-social & {
    bottom: -8px;
    left: 45px;
    @include mediaMax(991) {
      bottom: -48px;
      left: -15px;
    }
  }
  
  @at-root .post-template-new-blog-post & {
    bottom: -43px;
    left: -15px;
  }
  &:before {
    content: "";
    background-color: #263646;
    height: 10px;
    width: 10px;
    display: block;
    position: absolute;
    transform: rotate(45deg);
    @at-root .single-post__list-social & {
      top: 8px;
      left: -3px;
      @include mediaMax(991) {
        top: -5px;
        left: 30px;
      }
    }
    
    @at-root .post-template-new-blog-post & {
      top: 0;
      left: 30px;
    }
  }
}
